import React from "react";
import styled from "styled-components";
// Components
import PricingTable from "../Elements/PricingTable";

export default function Pricing() {
  return (
    <Wrapper id="pricing">
      <div className="whiteBg">
        <div className="container">
          <HeaderInfo>
            <h1 className="font40 extraBold"> </h1>
            <p className="font13">
                 
              <br />
              (             .)
            </p>
          </HeaderInfo>
          <TablesWrapper className="flexSpaceNull">
            <TableBox>
              <PricingTable
                icon="heatpump"
                price="80.00"
                title="  "
                text="    (1)    . ()"
                offers={[
                  { name: "  ", cheked: true },
                  { name: "  ", cheked: true },
                  { name: "- ", cheked: true },
                  { name: "  & ", cheked: true },
                ]}
                action={() => alert("clicked")}
              />
            </TableBox>
            <TableBox>
              <PricingTable
                icon="aircon"
                price="30.00"
                title="  "
                text="    (1)   split unit."
                offers={[
                  { name: "  ", cheked: true },
                  { name: "  ", cheked: true },
                  { name: "- ", cheked: true },
                  { name: "  & ", cheked: true },
                ]}
                action={() => alert("clicked")}
              />
            </TableBox>
            <TableBox>
              <PricingTable
                icon="whiteappliance"
                price="20.00"
                title="   "
                text="     (1)  . (     )"
                offers={[
                  { name: "/ ", cheked: true },
                  { name: "/ ", cheked: true },
                  { name: "/     ", cheked: true },
                ]}
                action={() => alert("clicked")}
              />
            </TableBox>
          </TablesWrapper>
        </div>
      </div>
    </Wrapper>
  );
}

const Wrapper = styled.section`
  width: 100%;
  padding: 50px 0;
`;
const HeaderInfo = styled.div`
  margin-bottom: 50px;
  @media (max-width: 860px) {
    text-align: center;
  }
`;
const TablesWrapper = styled.div`
  @media (max-width: 860px) {
    flex-direction: column;
  }
`;
const TableBox = styled.div`
  width: 31%;
  @media (max-width: 860px) {
    width: 100%;
    max-width: 370px;
    margin: 0 auto
  }
`;




